// @flow
import React, { Component } from "react";

import styles from "./index.css";
import { View } from "react-web-dom";
import InfoColumn from "../../public/info/infoColumn";

type Props = {
    refund_amount: number,
    user_reason: string,
    user_explain: string,
    user_images: Array<string>,
}
type State = {}
export default class OrderDetailOtherInfo extends Component<Props, State> {
    render() {
        const {
            refund_amount,
            user_reason,
            user_explain,
            user_images,
        } = this.props
        return (
            <View className={styles.infoWarp}>
                <p className={styles.infoTitle}></p>
                <InfoColumn
                    infoList={[
                        {
                            title: '',
                            info: refund_amount,
                        }, {
                            title: '',
                            info: user_reason,
                        }, {
                            title: '',
                            info: user_explain ? user_explain : '',
                        }, {
                            title: '',
                            images: Array.isArray(user_images) && user_images.length > 0 ? user_images.map((img) => {
                                return { img }
                            }) : []
                        }
                    ]}
                />
            </View>
        );
    }
}
