// @flow
import React, { Component } from "react";
import { connect } from "react-redux";
import { View } from "react-web-dom";
import BasicInfo from "./basicInfo";
import DetailInfo from "./detailInfo";
import GoodsInfo from "./goodsInfo";
import OperateInfo from "./operateInfo";
import TrackingInfo from "./trackingInfo";
import { publicFunction } from "../../../utils";
import { getOrderRefundInfo } from "../../../actions/order/refund"
import { dispatchType } from "../../../utils/flow";
import { Spin } from "antd";

const { parseQuery } = publicFunction

type Props = {
    dispatch: dispatchType,
    history: { push: Function },
    location: { state: { type: string, record: {} }, search: string },
    id: number,
    getOrderRefundInfo?: Function,
    orderRefundInfo: {
        id: number,// ID,
        order_id: number, // ID,
        order_sn: string, // ,
        order_state: number, // :20:;30:;40:;,
        order_goods_id: number, // ID,
        refund_sn: string, // ,
        user_id: number, // ID,
        user_name: string, // ,
        goods_id: number, // id,
        goods_sku_id: number, // ID,0,
        goods_title: string, // ,
        goods_spec: Array<{
            id: number,
            name: string,
            value_id: number,
            value_name: string
        }>, // ,
        goods_img: string, // ,
        goods_pay_price: number, // ,
        goods_num: number, // ,
        goods_freight_fee: number, // ,
        refund_type: number, // :1,2,1,
        refund_amount: number, //  ,
        order_amount: number, // ,
        order_lock: number, // :1,2,1,
        create_time: number, // ,
        user_reason: string, // ,
        user_explain: string, // ,
        tracking_no: string, //  ,
        tracking_phone: string, //  ,
        tracking_company: string, //  ,
        tracking_explain: string, //  ,
        tracking_images: Array<string>, //   6,
        tracking_time: number, //  ,0,
        receive: number, //  1 2,
        receive_time: number, // ,0,
        receive_message: string, // ,
        payment_code: string, // ,
        trade_no: string, // OR,
        handle_state: number, //  0() 10() 20 30() 50() 51(),
        handle_message: string, // ,
        handle_time: number, // ,
        batch_no: string, //  ,
        success_time: number, // ,
        user_receive: string, //  0(Order state=20) 1 2 (),
        user_images: Array<string>, //  ,
        is_close: number, // 0 1(),
        handle_expiry_time: number, // ,
        send_expiry_time: number, // ,
    },
}
type State = {}
@connect(
    ({ view: { order: { orderRefundInfo } } }) => ({
        orderRefundInfo,
    })
)
export default class RefundDetail extends Component<Props, State> {
    componentDidMount() {
        const { location, dispatch } = this.props
        const { id } = parseQuery(location.search)
        dispatch(getOrderRefundInfo({ params: { id } }))
    }

    render() {
        const { orderRefundInfo,history } = this.props
        const {
            id,
            refund_sn, refund_type, handle_state, handle_message, create_time,
            refund_amount, user_reason, user_explain, user_images,
            order_id,
            order_sn,
            goods_id, goods_title, goods_img, goods_spec, goods_pay_price, goods_num,

            tracking_no,
            tracking_phone,
            tracking_company,
            tracking_explain,
            tracking_time,
            tracking_images,
            receive,
            receive_time

        } = orderRefundInfo
        return (
            id > 0 ? <View>
                <BasicInfo
                    refund_sn={refund_sn}
                    refund_type={refund_type}
                    handle_state={handle_state}
                    handle_message={handle_message}
                    create_time={create_time}
                    order_id={order_id}
                    order_sn={order_sn}
                    history={history}
                />
                <GoodsInfo
                    goods_id={goods_id}
                    goods_title={goods_title}
                    goods_img={goods_img}
                    goods_spec={goods_spec}
                    goods_num={goods_num}
                    goods_pay_price={goods_pay_price}
                />
                <DetailInfo
                    refund_amount={refund_amount}
                    user_reason={user_reason}
                    user_explain={user_explain}
                    user_images={user_images}
                />
                {tracking_time > 0 ? <TrackingInfo
                    id={id}
                    tracking_no={tracking_no}
                    tracking_phone={tracking_phone}
                    tracking_company={tracking_company}
                    tracking_explain={tracking_explain}
                    tracking_time={tracking_time}
                    tracking_images={tracking_images}
                    receive={receive}
                    receive_time={receive_time}
                /> : null}

                <OperateInfo
                    id={id}
                    refund_amount={refund_amount}
                    handle_message={handle_message}
                    handle_state={handle_state}
                />
            </View> : <Spin tip="Loading..." spinning={true} />
        )
    }
}
